    isLogIn(uid, email) {
      this.setState({userId: uid});
      this.setState({userName: email.substring(0, email.indexOf('@'))});
      this.getResourcesByUidAxios(uid);
    }

    componentDidMount() {
          
        firebase.auth().onAuthStateChanged((user) => {
             if (user && localStorage.getItem("UserName")!==null && localStorage.getItem("UserName")!=="null") {
                // User logged in already or has just logged in.
                this.setState({userName: user.email.substring(0, user.email.indexOf('@'))});
                this.setState({userId: user.uid});
                this.setState({userEmail: user.email});
                this.getResourcesByUidAxios(this.state.userId);
            } else {
                // User not logged in or has just logged out.
                this.setState({userName: "Log in to display user name"});
                this.setState({userId: null});
                this.setState({userEmail: null});
                this.getResourcesAxios();
            }
        });
    }

    async getResourcesAxios(){
        const response =
          await axios.get("https://test2-plopyzswiq-uc.a.run.app/getResources")
        let tiles = [];
        let currentComponent = this;
        response.data.forEach(function(doc) {
            tiles.push({
                id: doc.id,
                title: doc.title,
                content: doc.content,
                imgURL: doc.imgURL,
                Category: doc.Category,
                URL: doc.URL,
                userId: currentComponent.state.userId
            });
        });
        tiles = tiles.slice(12)
        currentComponent.setState({tiles: tiles});
    }

    async getResourcesByUidAxios(userId){
        // let currentComponent = this;
        const response =
            //(force test) await axios.get(`http://localhost:8080/getResourcesByUid/CwDv5zmB2CZlM3mZrk3EXlWq5eR2`).then(res => {
            await axios.get(`https://test2-plopyzswiq-uc.a.run.app/getResourcesByUid/${userId}`).then(res => {
                console.log(res)
                let tiles = [];
                let currentComponent = this;
                res.data.forEach(function(doc) {
                    tiles.push({
                        id: doc.id,
                        title: doc.title,
                        content: doc.content,
                        imgURL: doc.imgURL,
                        Category: doc.Category,
                        URL: doc.URL,
                        userId: currentComponent.state.userId
                    });
                });
                currentComponent.setState({tiles: tiles});
            })
    }

